/**
 * @fileOverview Centralized type definitions and Zod schemas for all AI flows.
 * This file does NOT use 'use server' and can be safely imported on the client and server.
 */

import { z } from 'zod';

// For: generate-trending-keywords.ts
export const GenerateTrendingKeywordsInputSchema = z.object({
  businessType: z.string().describe('The type of business or industry, e.g., "SaaS for project management".'),
  country: z.string().describe('The target country for the analysis, e.g., "United States".'),
  city: z.string().optional().describe('The optional target city for more localized trends, e.g., "San Francisco".'),
});
export type GenerateTrendingKeywordsInput = z.infer<typeof GenerateTrendingKeywordsInputSchema>;

export const KeywordSchema = z.object({
  id: z.string().describe("A unique identifier for the keyword, like 'w1' or 'd2'."),
  name: z.string().describe("The trending keyword or phrase."),
  volume: z.number().describe("An estimated search volume or trend score, as an integer."),
  change: z.number().describe("The percentage change over the time period (e.g., 18 for 18%)."),
  difficulty: z.number().min(0).max(100).describe("An estimated SEO difficulty score from 0 to 100."),
  serpFeatures: z.array(z.string()).describe("A list of observed SERP features, like 'Featured Snippet', 'Local Pack', or 'Video Carousel'.")
});
export type Keyword = z.infer<typeof KeywordSchema>;

export const GenerateTrendingKeywordsOutputSchema = z.object({
  hour: z.array(KeywordSchema).describe("An array of 3-5 keywords trending in the past hour."),
  day: z.array(KeywordSchema).describe("An array of 3-5 keywords trending in the past day."),
  week: z.array(KeywordSchema).describe("An array of 5-10 keywords trending in the past week."),
  month: z.array(KeywordSchema).describe("An array of 5-10 keywords trending in the past month."),
});
export type GenerateTrendingKeywordsOutput = z.infer<typeof GenerateTrendingKeywordsOutputSchema>;


// For: translate-keywords-arabic.ts
export const TranslateKeywordsArabicInputSchema = z.object({
  businessType: z.string().describe('The type of business or industry, used for context.'),
  keywords: z.array(z.string()).describe('An array of English keywords to translate.'),
});
export type TranslateKeywordsArabicInput = z.infer<typeof TranslateKeywordsArabicInputSchema>;

export const TranslatedKeywordSchema = z.object({
  keyword: z.string().describe('The translated keyword in Arabic.'),
  volume: z.number().describe('Estimated search volume for the Arabic keyword in the KSA region.'),
  change: z.number().describe('Estimated trend change percentage for the Arabic keyword.'),
});

export const TranslateKeywordsArabicOutputSchema = z.object({
  translatedKeywords: z.array(TranslatedKeywordSchema).describe('An array of translated keywords with their KSA-specific trend data.'),
});
export type TranslateKeywordsArabicOutput = z.infer<typeof TranslateKeywordsArabicOutputSchema>;


// For: seo-content-suggestions.ts
export const SeoContentSuggestionsInputSchema = z.object({
  businessType: z.string().describe('The type of business or industry for context.'),
  trendingKeywords: z.string().describe('A comma-separated string of the top 3-5 trending keywords.'),
});
export type SeoContentSuggestionsInput = z.infer<typeof SeoContentSuggestionsInputSchema>;

export const SuggestionSchema = z.object({
    title: z.string().describe("A compelling, SEO-friendly title for a piece of content."),
    hook: z.string().describe("A 1-2 sentence engaging hook to draw the reader in."),
    points: z.array(z.string()).describe("An array of 3-5 key talking points or sections for the content brief."),
});

export const SeoContentSuggestionsOutputSchema = z.object({
  suggestions: z.array(SuggestionSchema).describe('An array of 2-3 content strategy briefs.'),
});
export type SeoContentSuggestionsOutput = z.infer<typeof SeoContentSuggestionsOutputSchema>;


// For: advanced-seo-keyword-analysis.ts
export const AdvancedSeoKeywordAnalysisInputSchema = z.object({
  businessType: z.string().describe('The type of business or industry for context.'),
  keyword: z.string().describe('The specific keyword for in-depth analysis.'),
});
export type AdvancedSeoKeywordAnalysisInput = z.infer<typeof AdvancedSeoKeywordAnalysisInputSchema>;

export const AdvancedSeoKeywordAnalysisOutputSchema = z.object({
  searchIntent: z.string().describe('The primary search intent (e.g., Informational, Commercial, Transactional, Navigational).'),
  targetAudience: z.string().describe('A description of the ideal target audience for this keyword.'),
  competitiveLandscape: z.string().describe('A brief analysis of the current top-ranking content and where the opportunities are.'),
  contentAngle: z.string().describe('A unique angle or perspective to make content stand out.'),
  longTailKeywords: z.array(z.string()).describe('A list of 3-5 related long-tail keyword variations.'),
  relatedQuestions: z.array(z.string()).describe("A list of 3-5 common questions users ask related to this keyword (for 'People Also Ask' sections)."),
  detailedContentOutline: z.object({
      title: z.string().describe('A fully-formed, compelling title for an article or blog post.'),
      sections: z.array(z.object({
          heading: z.string().describe('The heading for a section of the article.'),
          points: z.array(z.string()).describe('An array of key topics or bullet points to cover within that section.'),
      })).describe('An array of sections that make up the content outline.'),
  }).describe('A detailed outline for a piece of content, including a title and section breakdowns.'),
  difficultyAnalysis: z.string().describe('A qualitative analysis of why this keyword is easy, medium, or hard to rank for.'),
  confidenceScore: z.number().min(0).max(100).describe('A confidence score (0-100) representing the quality and certainty of the analysis.'),
  simulatedSources: z.array(z.string()).describe('An array of up to 3 simulated source URLs (e.g., "https://www.competitor.com/blog/related-topic", "https://www.industry-report.com/stats") that informed the analysis.'),
});
export type AdvancedSeoKeywordAnalysisOutput = z.infer<typeof AdvancedSeoKeywordAnalysisOutputSchema>;


// For: generate-chart-takeaway.ts
export const GenerateChartTakeawayInputSchema = z.object({
    businessType: z.string().describe('The business type for context.'),
    keywordChartData: z.string().describe('A JSON string representing the data shown in the chart, typically an array of objects with "name" and "volume" keys.'),
});
export type GenerateChartTakeawayInput = z.infer<typeof GenerateChartTakeawayInputSchema>;

export const GenerateChartTakeawayOutputSchema = z.object({
    takeaway: z.string().describe("A single, concise, CMO-level sentence summarizing the key insight or takeaway from the chart data."),
});
export type GenerateChartTakeawayOutput = z.infer<typeof GenerateChartTakeawayOutputSchema>;

// For: market-deep-dive.ts
export const MarketDeepDiveInputSchema = z.object({
  businessType: z.string().describe('The type of business or industry, e.g., "SaaS for project management".'),
  country: z.string().describe('The target country for the analysis, e.g., "United States".'),
  city: z.string().optional().describe('The optional target city for a more localized report, e.g., "San Francisco".'),
});
export type MarketDeepDiveInput = z.infer<typeof MarketDeepDiveInputSchema>;

const MarketValueSchema = z.object({
  value: z.number().describe('The estimated market value in USD.'),
  description: z.string().describe('A brief explanation of how this value was derived.'),
  sources: z.array(z.string()).describe('Simulated source URLs for this data point (e.g., https://www.gartner.com/en/documents/...).')
});

export const MarketDeepDiveOutputSchema = z.object({
    executiveSummary: z.string().describe("A C-suite level executive summary of the market analysis."),
    tamSamSom: z.object({
        tam: MarketValueSchema.describe('Total Addressable Market'),
        sam: MarketValueSchema.describe('Serviceable Available Market'),
        som: MarketValueSchema.describe('Serviceable Obtainable Market'),
    }).describe("TAM/SAM/SOM analysis with value, description, and simulated sources."),
    competitors: z.array(z.object({
        name: z.string().describe("Name of the competitor."),
        strengths: z.string().describe("Key strengths of the competitor."),
        weaknesses: z.string().describe("Key weaknesses of the competitor."),
        marketShare: z.string().describe("Estimated market share as a percentage string (e.g., '15%').")
    })).describe("A benchmark of 3-5 key competitors."),
    swot: z.object({
        strengths: z.array(z.string()).describe("A list of internal strengths for the business type in this market."),
        weaknesses: z.array(z.string()).describe("A list of internal weaknesses."),
        opportunities: z.array(z.string()).describe("A list of external opportunities in the market."),
        threats: z.array(z.string()).describe("A list of external threats in the market."),
    }).describe("A full SWOT analysis.")
});
export type MarketDeepDiveOutput = z.infer<typeof MarketDeepDiveOutputSchema>;
